<!--  -->
<template>
  <div class="commnet-wrap">
    <p class="ng-module-title clear">
      <span class="ng-list-title-main"></span>
      <router-link class="ng-list-more" to="/llo"></router-link>
    </p>
    <ul class="ng-ul-list-none comment-list-wrap">
      <li class="clear">
        <Avatar src="static/image/avatar/3.jpg" size="small" class="ng-f-l"></Avatar>
        <div class="ng-comment-content-wrap clear">
          <div class="ng-f-r">
            <p>     </p>
          </div>
        </div>
        <p class="text-align-r ng-comment-content-info"> <span></span>  <linkSite title="" to="/ll"></linkSite></p>
      </li>
      <li class="clear">
        <Avatar src="static/image/avatar/4.jpg" size="small" class="ng-f-l"></Avatar>
        <div class="ng-comment-content-wrap clear">
          <div class="ng-f-r">
            <p>              </p>
          </div>
        </div>
        <p class="text-align-r ng-comment-content-info"> <span>GL_open</span>  <linkSite title="" to="/ll"></linkSite></p>
      </li>
    </ul>
  </div>
</template>

<script>
import linkSite from '../../link/site'
export default {
  data () {
    return {
      data: {
      }
    }
  },
  components: {linkSite}
}
</script>

<style scoped>
.ng-comment-content-wrap{
}
.ng-comment-content-wrap > div{
  position: relative;
  width: calc(100% - 35px);
  background-color: #555;
  color:#f2f2f2;
  padding: 5px;
  border:1px solid #ececec;
  border-radius: 3px;
}
.ng-comment-content-wrap > div::before{
  content: '';
  width:0;
  height: 0;
  border-width: 5px 5px 5px 0;
  border-style: solid;
  border-color: transparent #555 transparent transparent;
  position: absolute;
  left: -5px;
  top:7px;
}
ul.comment-list-wrap > li{
  padding:0 0 10px 0;
}
.ng-comment-content-info{
  margin-top: 4px;
  font-style: italic;
  color: #666;
}
</style>



